

* {
  margin: 0;
}

body {
  background-color: #f5f6fa;
}

.app {
  display: flex;
  justify-content: space-evenly;
  background-color: #f5f6fa;
  padding: 20px;
}
.app__top{
  background-color: #f5f6fa;
  padding: 20px;
}
.app__bottom{
  display: flex;
  flex-direction: column;
}
.app__footer{
  display: flex;
  flex-direction: column;
}
.app__header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.app__dropdown{
  background-color: white;
}
.app__header > h1{
  color: #fc3c3f;
  font-size: 2rem;
}
.app__left{
  flex: 0.9;
}

.app__stats {
  display: flex;
  justify-content: space-between;
}
.app__right{
  display: flex;
  flex-direction: column;
}
.app__right .MuiCardContent-root{
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.app__graph{
  flex-grow: 1;
}

.app__graphTitle{
  margin-top: 20px;
  margin-bottom: 20px;
}
@media (max-width: 990px) {
  .app {
    flex-direction: column;
  }
}
